function save_customizations(class_id) {
    var levels = [];
    $('.level-select-button').each(function () {
        if ($(this).hasClass("green-btn")) {
            levels.push($(this).val());
        }
    });
    var adventures = {};
    $('.adventure_keys').each(function () {
        var name = $(this).attr('adventure');
        // @ts-ignore
        adventures[name] = [];
    });
    $('.adventure_level_input').each(function () {
        var name = $(this).attr('adventure');
        // @ts-ignore
        var current_list = adventures[name];
        if ($(this).prop("checked")) {
            current_list.push($(this).attr('level'));
            // @ts-ignore
            adventures[name] = current_list;
        }
    });
    var teacher_adventures = [];
    $('.teacher_adventures_checkbox').each(function () {
        if ($(this).prop("checked")) {
            teacher_adventures.push($(this).attr('id'));
        }
    });
    var other_settings = [];
    $('.other_settings_checkbox').each(function () {
        if ($(this).prop("checked")) {
            other_settings.push($(this).attr('id'));
        }
    });
    var opening_dates = {};
    $('.opening_date_container').each(function () {
        if ($(this).is(":visible")) {
            $(this).find(':input').each(function () {
                // @ts-ignore
                opening_dates[$(this).attr('level')] = $(this).val();
            });
        }
    });
    $.ajax({
        type: 'POST',
        url: '/for-teachers/customize-class/' + class_id,
        data: JSON.stringify({
            levels: levels,
            opening_dates: opening_dates,
            adventures: adventures,
            teacher_adventures: teacher_adventures,
            other_settings: other_settings
        }),
        contentType: 'application/json',
        dataType: 'json'
    }).done(function (response) {
        modal_1.modal.alert(response.success, 3000, false);
        window.State.unsaved_changes = false;
        $('#remove_customizations_button').removeClass('hidden');
    }).fail(function (err) {
        modal_1.modal.alert(err.responseText, 3000, true);
    });
}
exports.save_customizations = save_customizations;
