function create_accounts(prompt) {
    modal_1.modal.confirm(prompt, function () {
        $('#account_rows_container').find(':input').each(function () {
            $(this).removeClass('border-2 border-red-500');
        });
        var accounts = [];
        $('.account_row').each(function () {
            if ($(this).is(':visible')) { //We want to skip the hidden first "copy" row
                var account_1 = {};
                $(this).find(':input').each(function () {
                    // @ts-ignore -> Not sure why TypeScript has issues, this should be valid
                    account_1[$(this).attr("name")] = $(this).val();
                });
                accounts.push(account_1);
            }
        });
        $.ajax({
            type: 'POST',
            url: '/for-teachers/create-accounts',
            data: JSON.stringify({
                accounts: accounts
            }),
            contentType: 'application/json',
            dataType: 'json'
        }).done(function (response) {
            if (response.error) {
                modal_1.modal.alert(response.error, 3000, true);
                $('#account_rows_container').find(':input').each(function () {
                    if ($(this).val() == response.value) {
                        $(this).addClass('border-2 border-red-500');
                    }
                });
                return;
            }
            else {
                modal_1.modal.alert(response.success, 3000, false);
                if ($("input[name='download_credentials_checkbox']:checked").val() == "yes") {
                    download_login_credentials(accounts);
                }
                $('#account_rows_container').find(':input').each(function () {
                    $(this).val("");
                });
            }
        }).fail(function (err) {
            modal_1.modal.alert(err.responseText, 3000, true);
        });
    });
}
exports.create_accounts = create_accounts;
function download_login_credentials(accounts) {
    // https://stackoverflow.com/questions/14964035/how-to-export-javascript-array-info-to-csv-on-client-side
    var csvContent = "data:text/csv;charset=utf-8,";
    csvContent += "Username, Password" + "\r\n";
    accounts.forEach(function (account) {
        var row = account.username + "," + account.password;
        csvContent += row + "\r\n";
    });
    var encodedUri = encodeURI(csvContent);
    var link = document.createElement("a");
    link.setAttribute("href", encodedUri);
    link.setAttribute("download", "accounts.csv");
    document.body.appendChild(link); // Required for Firefox
    link.click();
}
function copy_join_link(link, success) {
    // https://qawithexperts.com/article/javascript/creating-copy-to-clipboard-using-javascript-or-jquery/364
    var sampleTextarea = document.createElement("textarea");
    document.body.appendChild(sampleTextarea);
    sampleTextarea.value = link;
    sampleTextarea.select();
    document.execCommand("copy");
    document.body.removeChild(sampleTextarea);
    modal_1.modal.alert(success, 3000, false);
}
exports.copy_join_link = copy_join_link;
