// *** Admin functionality ***
function markAsTeacher(checkbox, username, is_teacher) {
    $(checkbox).prop('checked', false);
    var text = "Are you sure you want to remove " + username + " as a teacher?";
    if (is_teacher) {
        text = "Are you sure you want to make " + username + " a teacher?";
    }
    modal_1.modal.confirm(text, function () {
        $.ajax({
            type: 'POST',
            url: '/admin/markAsTeacher',
            data: JSON.stringify({
                username: username,
                is_teacher: is_teacher
            }),
            contentType: 'application/json; charset=utf-8'
        }).done(function () {
            $(checkbox).prop('checked', true);
            modal_1.modal.alert(['User', username, 'successfully', is_teacher ? 'marked' : 'unmarked', 'as teacher'].join(' '), 2000, false);
        }).fail(function () {
            modal_1.modal.alert(['Error when', is_teacher ? 'marking' : 'unmarking', 'user', username, 'as teacher'].join(' '), 2000, false);
        });
    });
}
exports.markAsTeacher = markAsTeacher;
function changeUserEmail(username, email) {
    modal_1.modal.prompt('Please enter the corrected email', email, function (correctedEmail) {
        if (correctedEmail === email)
            return;
        $.ajax({
            type: 'POST',
            url: '/admin/changeUserEmail',
            data: JSON.stringify({
                username: username,
                email: correctedEmail
            }),
            contentType: 'application/json; charset=utf-8'
        }).done(function () {
            location.reload();
        }).fail(function () {
            modal_1.modal.alert(['Error when changing the email for user', username].join(' '), 2000, true);
        });
    });
}
exports.changeUserEmail = changeUserEmail;
